.container {
    width: 100%;
    height: 100vh;
    position: relative;
    font-family: 'Tenor Sans', sans-serif;
    display: flex;
    overflow: hidden;
}

.logo {
    color: white;
    font-size: 20px;
    position: absolute;
    z-index: 2;
    top: 15px;
    left: 50px;
}

.border {
    width: calc(100% - 50px);
    height: calc(100% - 100px);
    box-sizing: border-box;
    border: 1px solid white;
    border-right: 0;
    position: absolute;
    z-index: 1;
}

.left {
    padding: 50px 0 50px 50px;
    box-sizing: border-box;
    width: 50vw;
    height: 100%;
    background: #231E26;
    position: relative;
}

.menu {
    color: white;
    position: absolute;
    display: flex;
    flex-direction: row;
    list-style: none;
    top: 40%;
    transform: translate(calc(-50% - 25px), 50%) rotate(-90deg);
}

.menu li {
    margin-right: 40px;
    font-size: 18px;
    animation: text-in 1.5s ;
    transition: 1s;
}

.menu li:hover {
    transform: translateY(-20%);
}

.menu li:nth-child(2) {
    animation-delay: 0.5s;
}

.menu li:nth-child(3) {
    animation-delay: 1s;
}


.title {
    margin-top: 250px;
    height: 140px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title p {
    color: white;
    font-size: 120px;
    animation: titleAnimation 5s;
}

.right {
    box-sizing: border-box;
    width: 50vw;
    height: 100vh;
    background: white;
    position: relative;
    font-family: 'Tenor Sans', sans-serif;
}

.right img {
    width: 50vw;
    height: 100vh;
    object-fit: cover;
    object-position: top;
}


/*    Medium Devices, Desktops  */
@media only screen and (max-width: 1024px) {

}

/*    Small Devices, Tablets   */
@media only screen and (max-width: 768px) {
    .container {
        flex-wrap: wrap;
        height: calc(160vh + 60px);
    }

    .logo {
        background: #231E26;
        height: 60px;
        width: 100vw;
        top: 0;
        left: 0;
        font-size: 32px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .left {
        padding: 20px;
        width: 100vw;
        height: 80vh;
    }

    .border {
        border: 1px solid white;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }

    .menu {
        left: 50%;
        top: calc(100% - 50px);
        transform: translateX(-50%) rotate(0);
    }

    .title {

        height: 80px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .title p {
        font-size: 80px;
    }

    .right {
        box-sizing: border-box;
        width: 100vh;
        height: 80vh;
        position: relative;
    }

    .right img {
        width: 100%;
        height: 100%;
    }

}


@keyframes titleAnimation {
    0% {
        opacity: 0;
        margin-top: 50px;
    }
    20% {
        opacity: 1;
        margin-top: 0;
    }
    80% {
        opacity: 1;
        margin-top: 0;
    }
    100% {
        opacity: 0;
        margin-top: -50px;
    }
}

@keyframes text-in {
    0% {
        transform: translateY(-30%);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}