.container {
    background: #231E26;
    /*height: 300vh;*/
    width: 100vw;
    font-family: 'Tenor Sans', sans-serif;
    color: white;
    z-index: 20;
    position: relative;
}

.border {
    width: 100vw;
    margin: 0 auto;
    max-width: 1300px;
}

.title1 {
    padding-top: 50px;
    position: relative;
    display: block;
    font-size: 100px;
}

.title2 {
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
    display: block;
    font-size: 100px;
}

.subTitle1 {
    padding-top: 40px;
    max-width: 1050px;
    position: relative;
    display: block;
    font-size: 42px;
}

.substrateContainer {
    position: relative;
    display: flex;
    height: 120vh;
}

.substrate {
    left: 50%;
    transform: translateX(-50%) rotate(0);
    position: relative;
}

.model01,
.model02 {
    width: 100%;
    position: relative;
    height: 50vw;
    box-sizing: border-box;
    text-align: center;
}

.model01 {
    padding-right: 10px;
    padding-left: 20px;
}

.model02 {
    padding-right: 20px;
    padding-left: 10px;
}

.model01 img,
.model02 img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 20px;
}

.modelTitleLeft {
    font-size: 55px;
    margin-top: 60px;
}

.modelTitleRight {
    font-size: 55px;
    margin-bottom: 60px;
}

/*    Medium Devices, Desktops  */
@media only screen and (max-width: 1024px) {

}

/*    Small Devices, Tablets   */
@media only screen and (max-width: 768px) {
    .border {
        padding: 0 20px;
        box-sizing: border-box;
    }

    .title1 {
        font-size: 60px;
    }

    .subTitle1 {
        font-size: 28px;
    }

    .title2 {
        padding-top: 50px;
        padding-bottom: 50px;
        font-size: 60px;
        text-align: center;
    }

    .substrateContainer {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        height: calc(200vw + 220px);
    }

    .model01 {
        margin-top: 40px;
    }

    .model01,
    .model02 {
        padding: 20px;
        width: 100vw;
        height: 100vw;
    }

    .modelTitleLeft {
        font-size: 32px;
        position: absolute;
        top: -90px;
    }

    .modelTitleRight {
        font-size: 32px;
        margin-bottom: 20px;
    }
}

/*    Extra Small Devices, Phones  */
@media only screen and (max-width: 480px) {

}


@keyframes rotate-substrate {
    from {
        transform: translateX(-50%) rotate(-23.5deg);
    }
    to {
        transform: translateX(-50%) rotate(0);
    }
}

@keyframes rotate-substrate01 {
    from {
        top: 150px;
    }
    to {
        top: 0px;
    }
}

@keyframes rotate-substrate02 {
    from {
        top: 0px;
    }
    to {
        top: 150px;
    }
}


